import React, { useEffect, useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import { getApp } from '../../../store/slices/app.slice';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {
    onComplete: () => void
}

const LensTransitionInAnimation : React.FC<Props>  = ({
    onComplete
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('lens-transition-in-animation');
    const { assets } = useSelector(getApp);
    const [fadeOut, setFadeOut] = useState(false);
    const imageRef = useRef<any>();
    let animationFrame:any = null;
    let imageIndex = 0;

    // HOOKS
    useEffect(() => {
        animationFrame = requestAnimationFrame(animate);
        return () => {
            cancelAnimationFrame(animationFrame);
        }
    }, [])
    

    // METHODS
    const animate = () => {
        animationFrame = requestAnimationFrame(animate);
        imageIndex += 1;
        if(imageIndex > 50){
            cancelAnimationFrame(animationFrame);
            setFadeOut(true);

            setTimeout(() => {
                onComplete();
            }, 500);

        } else {
            try {
                imageRef.current.src = assets[`transitionIn${imageIndex}`].blob;                
            } catch (error) {
                console.log(error);
            }
        }
    }

    if(!assets)return null;

    return (
        <div className={B( fadeOut? 'fade-out' : '' )}>
            <img src={assets[`transitionIn0`].blob} alt="" ref={imageRef}/>
        </div>
    )
}

export default LensTransitionInAnimation;