import React, { useEffect, useState } from 'react';
import useBEM from '../../../utils/hooks/useBEM';
import { useSelector } from 'react-redux';
import { getLensConsultation } from '../../../store/slices/lens-consultation.slice';
import { getApp } from '../../../store/slices/app.slice';
import Slider from "react-input-slider";
import Icon from '../../../components/ui/Icon';


interface Props {

}

type imageType = 'all' | 'correct' | 'protect' | 'enhance' | 'lensTransitionGreen';

const LensAnimation : React.FC<Props>  = () =>  {
    // VARIABLES
    const { assets } = useSelector(getApp);
    const [B, E] = useBEM('lens-animation');
    const [activeImage, setActiveImage] = useState<imageType>();
    const [images, setImages] = useState<{
        all: string,
        correct: string,
        protect: string,
        enhance: string,
        lensTransitionGreen: string,
        lensTransitionAmber: string,
        lensTransitionAmethyst: string,
        lensTransitionBrown: string,
        lensTransitionEmerald: string,
        lensTransitionGrey: string,
        lensTransitionSapphire: string,

        lensPolarizedAmber: string,
        lensPolarizedBlueInk: string,
        lensPolarizedBluePurple: string,
        lensPolarizedBlueSky: string,
        lensPolarizedBrown: string,
        lensPolarizedGrey: string,
        lensPolarizedGreyGreen: string,
        lensPolarizedHerbal: string,
        lensPolarizedViolet: string,

        lensTintedBlack: string,
        lensTintedBlueInk: string,
        lensTintedBrown: string,
        lensTintedBurgundy: string,
        lensTintedGreyGreen: string,
        lensTintedGrey: string,
        lensTintedHerbal: string,
        lensTintedNewBrown: string,

        lensMirrorBronzeShadow: string,
        lensMirrorBronze: string,
        lensMirrorCandy: string,
        lensMirrorForestGreen: string,
        lensMirrorGoldPink: string,
        lensMirrorGreenShadow: string,
        lensMirrorKaki: string,
        lensMirrorMauveSilver: string,
        lensMirrorNewBlue: string,
        lensMirrorNewSilver: string,
        lensMirrorOceanBlue: string,
        lensMirrorOrange: string,
        lensMirrorParma: string,
        lensMirrorPinkSilver: string,
        lensMirrorSilverShadow: string,
        lensMirrorSilver: string,
        lensMirrorSkyBlueSilver: string,
        lensMirrorViolet: string
    }>();

    const { productFamily, sliderPage } = useSelector(getLensConsultation);
    const { lensRecommendation, activePair } = useSelector(getLensConsultation);

    const [sliderValue, setSliderValue] = useState(100);
    const [show, setShow] = useState(false);
    const [isProtectXperio, setIsProtectXperio] = useState(false);
    const [withLensSlider, setWithLensSlider] = useState(false);

    const sliderStyle = {
        active: {
            backgroundColor: "CCE7F7",
        },
        thumb: {
            width: 24,
            height: 24,
            opacity: 1,
        }
    }
    

    // HOOKS
    useEffect(() => {
        setTimeout(() => {
            setShow(true);
        }, 500);
    },[])
    useEffect(() => {
        setImagesAssets();
    }, [assets]);

    useEffect(() => {
        const isExperio = isXperio();
        setIsProtectXperio(isExperio);
        setSliderValue(isExperio ? 4 : 100);
    }, [withLensSlider, activePair])

    useEffect(() => {
        if(productFamily){
            setActiveImage(productFamily?.id);
            setWithLensSlider(false);
        } else {
            const protect = getProtectLensImage();
            const imageId = protect ? protect.color?.id as imageType : null;
            setActiveImage(imageId ? imageId : 'all');
            setWithLensSlider(imageId ? true : false);
        }
    }, [productFamily]);

    useEffect(() => {
        const protect = getProtectLensImage();
        const imageId = protect ? protect.color?.id as imageType : null;
        if(!imageId){
            setWithLensSlider(false);
            setActiveImage('all');
            return;
        }
        setActiveImage(imageId);
        setWithLensSlider(true);
    }, [lensRecommendation, activePair])


    // METHODS
    const isXperio = () => {
        const protect:any = getProtectLensImage();
        if(!protect)return false;
        return String(protect?.product.label).toLocaleLowerCase().indexOf('xperio') >= 0;
    }

    const setImagesAssets = async () => {
        if(!assets || !assets.lensAll)return;

        const all = assets.lensAll.blob;
        const correct = assets.lensCorrectLens.blob;
        const protect = assets.lensProtectLens.blob;
        const enhance = assets.lensEnhanceLens.blob;
        const lensTransitionGreen = assets.lensTransitionGreen.blob;
        const lensTransitionAmber = assets.lensTransitionAmber.blob;
        const lensTransitionAmethyst = assets.lensTransitionAmethyst.blob;
        const lensTransitionBrown = assets.lensTransitionBrown.blob;
        const lensTransitionEmerald = assets.lensTransitionEmerald.blob;
        const lensTransitionGrey = assets.lensTransitionGrey.blob;
        const lensTransitionSapphire = assets.lensTransitionSapphire.blob;

        const lensPolarizedAmber = assets.lensPolarizedAmber.blob;
        const lensPolarizedBlueInk = assets.lensPolarizedBlueInk.blob;
        const lensPolarizedBluePurple = assets.lensPolarizedBluePurple.blob;
        const lensPolarizedBlueSky = assets.lensPolarizedBlueSky.blob;
        const lensPolarizedBrown = assets.lensPolarizedBrown.blob;
        const lensPolarizedGrey = assets.lensPolarizedGrey.blob;
        const lensPolarizedGreyGreen = assets.lensPolarizedGreyGreen.blob;
        const lensPolarizedHerbal = assets.lensPolarizedHerbal.blob;
        const lensPolarizedViolet = assets.lensPolarizedViolet.blob;

        const lensTintedBlack = assets.lensTintedBlack.blob;
        const lensTintedBlueInk = assets.lensTintedBlueInk.blob;
        const lensTintedBrown = assets.lensTintedBrown.blob;
        const lensTintedBurgundy = assets.lensTintedBurgundy.blob;
        const lensTintedGreyGreen = assets.lensTintedGreyGreen.blob;
        const lensTintedGrey = assets.lensTintedGrey.blob;
        const lensTintedHerbal = assets.lensTintedHerbal.blob;
        const lensTintedNewBrown = assets.lensTintedNewBrown.blob;

        const lensMirrorBronzeShadow = assets.lensMirrorBronzeShadow.blob;
        const lensMirrorBronze = assets.lensMirrorBronze.blob;
        const lensMirrorCandy = assets.lensMirrorCandy.blob;
        const lensMirrorForestGreen = assets.lensMirrorForestGreen.blob;
        const lensMirrorGoldPink = assets.lensMirrorGoldPink.blob;
        const lensMirrorGreenShadow = assets.lensMirrorGreenShadow.blob;
        const lensMirrorKaki = assets.lensMirrorKaki.blob;
        const lensMirrorMauveSilver = assets.lensMirrorMauveSilver.blob;
        const lensMirrorNewBlue = assets.lensMirrorNewBlue.blob;
        const lensMirrorNewSilver = assets.lensMirrorNewSilver.blob;
        const lensMirrorOceanBlue = assets.lensMirrorOceanBlue.blob;
        const lensMirrorOrange = assets.lensMirrorOrange.blob;
        const lensMirrorParma = assets.lensMirrorParma.blob;
        const lensMirrorPinkSilver = assets.lensMirrorPinkSilver.blob;
        const lensMirrorSilverShadow = assets.lensMirrorSilverShadow.blob;
        const lensMirrorSilver = assets.lensMirrorSilver.blob;
        const lensMirrorSkyBlueSilver = assets.lensMirrorSkyBlueSilver.blob;
        const lensMirrorViolet = assets.lensMirrorViolet.blob;
  
        
        setImages({
            lensTransitionAmber,
            lensTransitionAmethyst,
            lensTransitionBrown,
            lensTransitionEmerald,
            lensTransitionGrey,
            lensTransitionSapphire,
            lensTransitionGreen,

            lensPolarizedAmber,
            lensPolarizedBlueInk,
            lensPolarizedBluePurple,
            lensPolarizedBlueSky,
            lensPolarizedBrown,
            lensPolarizedGrey,
            lensPolarizedGreyGreen,
            lensPolarizedHerbal,
            lensPolarizedViolet,

            lensTintedBlack,
            lensTintedBlueInk,
            lensTintedBrown,
            lensTintedBurgundy,
            lensTintedGreyGreen,
            lensTintedGrey,
            lensTintedHerbal,
            lensTintedNewBrown,

            lensMirrorBronzeShadow,
            lensMirrorBronze,
            lensMirrorCandy,
            lensMirrorForestGreen,
            lensMirrorGoldPink,
            lensMirrorGreenShadow,
            lensMirrorKaki,
            lensMirrorMauveSilver,
            lensMirrorNewBlue,
            lensMirrorNewSilver,
            lensMirrorOceanBlue,
            lensMirrorOrange,
            lensMirrorParma,
            lensMirrorPinkSilver,
            lensMirrorSilverShadow,
            lensMirrorSilver,
            lensMirrorSkyBlueSilver,
            lensMirrorViolet,

            correct, 
            protect, 
            enhance, 
            all
        });
    }

    const getProtectLensImage = () => {
        if(!lensRecommendation)return '';
        const protect = lensRecommendation[activePair]?.protect;
        if(!protect || protect.length === 0)return '';
        // return protect[0]?.color?.id as imageType;
        return protect[0];
    }

    const setLensOpacity = (value: number) => setSliderValue(value);

    const generateTransitionLensOpacity = (imageKey:imageType) => {
        if(!withLensSlider || activeImage !== (imageKey as imageType))return {};
        return {
            opacity: sliderValue / (isProtectXperio ? 4 : 100)
        }
    }

    if(!images || !show)return null;

    return (
        <>
            { assets && 
                <div className={B()}>    

                    <img src={images.all} alt="" className={E('img')}  style={{
                        opacity: withLensSlider ? 1 : 0
                    }}/>        

                    {Object.keys(images).map( (imgKey: string, index: number) => (
                        <img   
                            style={generateTransitionLensOpacity(imgKey as imageType)}
                            src={images[imgKey as imageType]} 
                            alt="" 
                            key={index} 
                            className={E('img', activeImage === (imgKey as imageType) ? 'active' : '')}
                        />                    
                    ))}    
                </div> 
            }

            {
                withLensSlider && sliderPage < 3 &&
                <div className={E('slider', isProtectXperio ? 'xperio' : '')}>
                    <Icon name="sun-high"/>
                    <Slider
                        styles={sliderStyle}
                        axis="y"
                        ystep={1}
                        ymin={isProtectXperio ? 4 : 100}
                        ymax={isProtectXperio ? 1 : 10}
                        y={sliderValue}
                        onChange={({ y }) => {setLensOpacity(y)}}
                    />
                    <Icon name="sun-low"/>
                </div>
            }
            
        </>
        
    )
}

export default LensAnimation;