import React, { useEffect, useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import { getApp } from '../../../store/slices/app.slice';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {
    onComplete: () => void,
    type: 'Convexe' | 'Concave'
}

const LensTransitionOut : React.FC<Props>  = ({
    onComplete,
    type
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('lens-transition-out');
    const { assets } = useSelector(getApp);
    const imageRef = useRef<any>();
    let animationFrame = null;
    let imageIndex = 0;

    // HOOKS
    useEffect(() => {

        animationFrame = requestAnimationFrame(animate);

    }, [])
    

    // METHODS
    const animate = () => {
        animationFrame = requestAnimationFrame(animate);
        imageIndex += 1;
        if(imageIndex > 49){
            cancelAnimationFrame(animationFrame);
            onComplete();
        } else {
            imageRef.current.src = assets[`transitionOut${type}${imageIndex}`].blob;
        }
    }

    if(!assets)return null;

    return (
        <div className={B()}>
            <img src={assets[`transitionOut${type}0`].blob} alt="" ref={imageRef}/>
        </div>
    )
}

export default LensTransitionOut;